"use clien"
import React, { useState } from 'react';
import {
  Box,
  Grid,
  Card,
  CardMedia,
  CardContent,
  Typography,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  Pagination,
} from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';

const PropertyListing = () => {
  const [sort, setSort] = useState('Default');

  const handleSortChange = (event) => {
    setSort(event.target.value);
  };

  const properties = [
    {
      id: 1,
      status: 'For Sale',
      title: 'Diamond Manor Apartment',
      location: '22-05 Astoria Blvd, Astoria',
      beds: 4,
      baths: 2,
      sqft: 150,
      price: 6500,
      yearsAgo: 4,
      image: 'https://via.placeholder.com/300x200', // Replace with actual image URL
    },
    // Add more properties as needed
  ];

  return (
    <Box sx={{ padding: 3 }}>
      {/* Filter and Sort Section */}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          mb: 2,
        }}
      >
        <Typography variant="body1">
          Showing 1 â€“ {properties.length} of {properties.length} results
        </Typography>
        <FormControl variant="outlined" size="small">
          <InputLabel>Sort by</InputLabel>
          <Select value={sort} onChange={handleSortChange} label="Sort by">
            <MenuItem value="Default">Default</MenuItem>
            <MenuItem value="Price">Price</MenuItem>
            <MenuItem value="Newest">Newest</MenuItem>
          </Select>
        </FormControl>
      </Box>

      {/* Property Card Section */}
      <Grid container spacing={3}>
        {properties.map((property) => (
          <Grid item xs={12} sm={6} md={4} key={property.id}>
            <Card sx={{ display: 'flex', flexDirection: 'column' }}>
              <Box sx={{ position: 'relative' }}>
                <CardMedia
                  component="img"
                  height="200"
                  image={property.image}
                  alt={property.title}
                />
                <Typography
                  variant="body2"
                  sx={{
                    position: 'absolute',
                    top: 10,
                    left: 10,
                    backgroundColor: 'primary.main',
                    color: '#fff',
                    px: 1,
                    borderRadius: 1,
                  }}
                >
                  {property.status}
                </Typography>
              </Box>
              <CardContent>
                <Typography variant="subtitle2" color="textSecondary">
                  Apartment
                </Typography>
                <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                  {property.title}
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    mb: 1,
                    color: 'text.secondary',
                  }}
                >
                  <LocationOnIcon fontSize="small" sx={{ mr: 0.5 }} />
                  {property.location}
                </Typography>
                <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                  <Typography variant="body2">Beds: {property.beds}</Typography>
                  <Typography variant="body2">Baths: {property.baths}</Typography>
                  <Typography variant="body2">Sqft: {property.sqft}</Typography>
                </Box>
              </CardContent>
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  px: 2,
                  pb: 2,
                  mt: 'auto',
                }}
              >
                <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                  ${property.price}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  {property.yearsAgo} years ago
                </Typography>
              </Box>
            </Card>
          </Grid>
        ))}
      </Grid>

      {/* Pagination Section */}
      <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center' }}>
        <Pagination count={10} color="primary" />
      </Box>
    </Box>
  );
};

export default PropertyListing;
